@charset "UTF-8";
@font-face {
  font-family: caros-bold;
  src: url(../webfonts/caros_bold-webfont.woff) format("woff");
}

@font-face {
  font-family: caros-webfont;
  src: url(../webfonts/caros-webfont.woff) format("woff");
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

main {
  overflow: hidden;
}

header nav {
  padding: 60px 0;
}

header nav .container {
  display: flex;
  align-items: center;
}

header nav .logo {
  text-align: left;
}

header nav .logo a {
  font-family: "caros-bold", sans-serif;
  font-size: 26px;
  color: #283a5e;
}

header nav .menu-toggle {
  display: none;
}

header nav .navigation .logo {
  display: none;
}

header nav .navigation.hide {
  display: none;
}

header nav .navigation ul {
  display: flex;
}

header nav .navigation ul li {
  flex: 1;
}

header nav .navigation ul li a {
  font-family: "caros-webfont", sans-serif;
  color: #283a5e;
  transition: all .2s ease-in-out;
}

header nav .navigation ul li a:hover {
  color: #ff6d34;
}

header nav .contact {
  text-align: right;
}

header nav .contact.hide {
  display: none;
}

header nav .contact span {
  font-family: "caros-webfont", sans-serif;
  color: #828893;
}

header nav .contact .number {
  font-weight: 600;
  color: #ff6d34;
}

header nav .contact a {
  font-family: "caros-bold", sans-serif;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 5px 30px 0 #eff1f6;
  color: #ff6d34;
  padding: 17px 30px 17px 30px;
  margin-left: 55px;
  transition: all .2s ease-in-out;
}

header nav .contact a:hover {
  background-color: #ff6d34;
  color: #fff;
}

header nav .contact a:after {
  font-size: 10px;
  margin-left: 6px;
}

header.mobile {
  border-bottom: 1px solid #eee;
}

header.mobile nav {
  padding: 28px 0;
}

header.mobile nav .container {
  justify-content: space-between;
}

@media (min-width: 576px) {
  header.mobile nav .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  header.mobile nav .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  header.mobile nav .container {
    max-width: 960px;
  }
}

header.mobile nav .navigation {
  position: fixed;
  top: 0;
  left: -400px;
  width: 400px;
  height: 100%;
  max-width: 90%;
  background-color: #fff;
  z-index: 999;
  transition: all 0.7s cubic-bezier(0.19, 1, 0.22, 1);
}

header.mobile nav .navigation.show {
  display: block;
}

header.mobile nav .navigation.slide {
  left: 0;
}

header.mobile nav .navigation ul {
  display: block;
}

header.mobile nav .navigation ul li {
  padding: 15px;
}

header.mobile nav .navigation ul li:not(:last-of-type) {
  border-bottom: 1px solid #eee;
}

header.mobile nav .navigation ul li.logo.show {
  position: relative;
  display: block;
  padding: 20px 15px;
}

header.mobile nav .navigation ul li.logo.show span {
  transition: all .2s ease-in-out;
}

header.mobile nav .navigation ul li.logo.show span:hover {
  color: #ff6d34;
}

header.mobile nav .navigation ul li.logo.show span:after {
  content: '✕';
  position: absolute;
  right: 15px;
  top: 25px;
  cursor: pointer;
  width: 25px;
  height: 25px;
}

header.mobile nav .navigation ul li.logo.show a {
  font-family: "Poppins", sans-serif;
  font-size: 26px;
  font-weight: 600;
}

header.mobile nav .navigation ul li.logo.show a:hover {
  color: #283a5e;
}

header.mobile nav .navigation ul li a {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  font-weight: 500;
  color: #283a5e;
}

header.mobile nav .navigation ul li a:hover {
  color: #ff6d34;
}

header.mobile nav .menu-toggle {
  display: block;
  position: relative;
  width: 24px;
  height: 18px;
  cursor: pointer;
  z-index: 777;
}

header.mobile nav .menu-toggle span {
  position: absolute;
  top: 50%;
  left: 0;
  background-color: #222;
  width: 100%;
  height: 2px;
}

header.mobile nav .menu-toggle span:before, header.mobile nav .menu-toggle span:after {
  content: "";
  display: inline-block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #222;
}

header.mobile nav .menu-toggle span:before {
  top: -8px;
}

header.mobile nav .menu-toggle span:after {
  top: 8px;
}

footer {
  padding: 100px 0 80px;
  position: relative;
}

footer h2 {
  font-family: "caros-bold", sans-serif;
  color: #283a5e;
}

footer .site-name {
  font-size: 36px;
  letter-spacing: -1.06px;
  margin: -6px 0 25px;
}

footer p, footer a, footer span {
  font-family: "caros-webfont", sans-serif;
  font-weight: 400;
  line-height: 26px;
  color: #828893;
}

@media (max-width: 991px) {
  footer p, footer a, footer span {
    font-size: 14px;
  }
}

@media (min-width: 768px) {
  footer .content {
    padding-left: 30px;
  }
}

@media (max-width: 767px) {
  footer .content {
    margin-bottom: 30px;
  }
}

footer .content .title {
  font-size: 18px;
  letter-spacing: -0.36px;
  margin-bottom: 26px;
}

footer .content li {
  margin-bottom: 10px;
}

footer .content li a {
  padding: 10px 5px;
  transition: all .2s ease-in-out;
}

footer .content li a:hover {
  color: #ff6d34;
}

footer .copyright {
  margin-top: 5px;
}

@media (min-width: 768px) {
  footer .copyright {
    padding-top: 50px;
  }
}

footer .copyright nav {
  padding: 0;
}

@media (max-width: 767px) {
  footer .copyright nav {
    display: none;
  }
}

footer .copyright nav a {
  margin: 0 -1px 0 30px;
}

@media (min-width: 768px) {
  footer .copyright .socials {
    padding-left: 25px;
  }
}

@media (max-width: 767px) {
  footer .copyright .socials {
    margin-top: 25px;
  }
}

footer .copyright .socials a {
  margin: 0 8px;
  font-size: 16px;
  transition: all .2s ease-in-out;
}

footer .copyright .socials a:hover {
  color: #ff6d34;
}

footer .svg {
  overflow: hidden;
  position: absolute;
  bottom: -1px;
  width: 100%;
  transform: rotate(180deg);
  z-index: -999;
}

footer .svg svg {
  display: block;
  height: 500px;
  transform: translateX(-50%) rotateY(180deg);
  position: relative;
  left: 50%;
}

footer .svg svg path {
  fill: rgba(248, 245, 252, 0.56);
  transform-origin: center;
}

.window-size {
  position: absolute;
  top: 10px;
  right: 10px;
  color: red;
  font-size: 18px;
}

.overlay {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 888;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.3);
  transition: all .3s ease-in-out;
}

.overlay.opacity {
  opacity: 1;
}

.overlay.show {
  display: block;
}

