// add overlay when mobile menu opened
.overlay {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 888;
  opacity: 0;
  background-color: rgba(0,0,0,.3);
  transition: all .3s ease-in-out; }
.overlay.opacity {
  opacity: 1; }
.overlay.show {
  display: block; }
// Start getting-started section
