// Start our-services section
.our-services {
  padding: 12px 0;
  text-align: center;
  h2 {
    font-family: "caros-webfont", sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: $orangeColor;
    margin-bottom: 18px; }
  h3 {
    font-family: "caros-bold", sans-serif;
    font-size: 36px;
    color: $primaryColor;
    margin-bottom: 30px;
    line-height: 42px;
    @include maxMedium {
      font-size: 25px;
      line-height: 30px;
      margin-bottom: 10px; } }
  .services {
    padding: 26px 0;
    text-align: left;
    .service {
      background-color: #ffffff;
      border-radius: 5px;
      box-shadow: 0 5px 15px 0 rgba(130,136,147,0.1);
      padding: 60px 40px;
      margin: 0 15px;
      @include maxMedium {
        margin-bottom: 30px; }
      &:hover {
        .yellow {
          background-color: #ffdd6e;
          color: #fff; }
        .navy {
          background-color: #9365d4;
          color: #fff; }
        .orange {
          background-color: #ff6d34;
          color: #fff; } }
      .circle {
        position: relative;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        margin-bottom: 44px;
        transition: .3s ease-in-out;
        &:after {
          position: absolute;
          font-size: 32px;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%); } }
      .yellow {
        background-color: #fff7df;
        color: #ffdd6e;
        &:after {
          content: "\f103"; } }
      .navy {
        background-color: #f8f1ff;
        color: #9365d4;
        &:after {
          content: "\f102"; } }
      .orange {
        background-color: #ffeee4;
        color: #ff6d34;
        &:after {
          content: "\f104"; } }
      h4 {
        font-family: "caros-bold", sans-serif;
        font-size: 18px;
        line-height: 24px;
        letter-spacing: -0.36px;
        color: $primaryColor;
        margin-bottom: 15px; }
      p {
        font-family: "caros-webfont", sans-serif;
        font-size: 13px;
        line-height: 23px;
        color: $secondaryColor;
        margin-bottom: 15px; }
      a {
        font-family: "caros-bold", sans-serif;
        font-size: 13px;
        line-height: 20px;
        letter-spacing: -.02px;
        color: $orangeColor;
        transition: all .3s ease-in-out;
        &:hover {
          color: $primaryColor; } } } } }
// End our-services section
// Start about-us section
