@each $prefix, $size in (null: null, sm-: 576px, md-: 768px, lg-: 992px, xl-: 1200px) {
  @if $prefix == null {
    @for $i from 1 through 12 {
      .col-#{$prefix}#{$i} {
        flex: 0 0 percentage($i / 12);
        max-width: percentage($i / 12); } }
    .#{$prefix}flex {
      display: flex; }
    .#{$prefix}block {
      display: block; }
    .#{$prefix}inline-block {
      display: inline-block; } }
  @else {
    @media (min-width: $size) {
      @for $i from 1 through 12 {
        .col-#{$prefix}#{$i} {
          flex: 0 0 percentage($i / 12);
          max-width: percentage($i / 12); } }
      .#{$prefix}flex {
        display: flex; }
      .#{$prefix}block {
        display: block; }
      .#{$prefix}inline-block {
        display: inline-block; } } } }
.container {
  max-width: 1170px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

.image.lazyload {
  background-image: none !important;
  background-color: #F1F1FA; }

.heading-titles {
  h2 {
    font-family: "caros-webfont", sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: $orangeColor; }
  h3 {
    font-family: "caros-bold", sans-serif;
    font-size: 36px;
    color: $primaryColor;
    line-height: 42px;
    @include maxLarge {
      font-size: 30px;
      line-height: 35px; }
    @include maxMedium {
      font-size: 25px;
      line-height: 30px; } }
  .view-more {
    display: inline-block;
    font-family: "caros-bold", sans-serif;
    font-size: 15px;
    font-weight: 400;
    color: $orangeColor;
    margin-top: 80px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    &:hover {
      color: $primaryColor; } } }

